
import { providers, Contract, Wallet as ethersWallet } from 'ethers'
import WalletConnectProvider from '@walletconnect/web3-provider/dist/umd/index.min';
import { Tips } from '../../libs/utils'
import { ElNotification } from 'element-plus'
import BLKTokenABI from '@/api/abis/IERC20.json'
import WETHABI from '@/api/abis/doge.json'
import Events from './event'
import { toRaw } from 'vue'
const { ethereum } = window
class Wallet extends Events {
  constructor(Vue, network) {
    super()
    this.hasEthereum = typeof ethereum !== 'undefined'
    this.accounts = []
    // 当前连接状态 1：metamask 2：wallet 3：私钥
    this.connectState = JSON.parse(localStorage.getItem('connectState')) || false
    this.connectKey = localStorage.getItem('connectKey') || ''
    this.network = network
    this.dex = network.Dex[0]
    this.init()
  }

  // 连接到metaMask
  async _connectMetaMask() {
    if (!ethereum) {
      ElNotification({
        title: 'Tips',
        message: `Please install the wallet first`,
        type: 'error',
      })
      return
    }
    ethereum.on('connect', () => {
      const timer = setInterval(() => {
        if (ethereum.isConnected()) {
          clearInterval(timer)
        }
        if (ethereum.chainId === this.network.ChainID && ethereum.selectedAddress) {
          this.connect(this.connectState)
        }
      }, 1000)
    });

    // 监听链路变动
    ethereum.on('chainChanged', (_chainId) => {
      // 如果当前链路跟当前环境链路不一致，则退出，并提示用户
      if (this.network.ChainID != _chainId) {
        ElNotification({
          title: 'Tips',
          message: `Current wallet does not support, please switch to ${this.network.Name}`,
          type: 'error',
        })
        this.logout()
      } else {
        this.connect(this.connectState)
      }
    })
    if (!this.checkEthereum()) {
      return
    }
    const bool = await this.wallet_switchEthereumChain()
    if (!bool) {
      return
    }
    const accounts = await ethereum.request({ method: 'eth_requestAccounts' }).catch(error => {
      Tips(error)
    })
    this.provider = new providers.Web3Provider(ethereum).getSigner()
    this.accountsChanged(accounts)
    // 监听账号变动
    ethereum.on('accountsChanged', (data) => {
      this.accountsChanged(data)
    })
    this.connectState = 1
    localStorage.setItem('connectState', 1)
    return accounts
  }

  // 私钥登录
  _connectPrivateKey(key) {
    try {
      this.provider = new ethersWallet(
        key,
        new providers.JsonRpcProvider(this.network.Rpcs[0])
      );
      this.connectState = 3
      localStorage.setItem('connectState', 3)
      localStorage.setItem('connectKey', key)
      this.accountsChanged([this.provider.address]) 
    } catch (error) {
      Tips(error) 
    }
  }

  // walletConnect连接
  async _walletConnect() {
    const walletConnect = new WalletConnectProvider({
      rpc: {
        56: 'https://rpc.ankr.com/bsc',
      },
      qrcodeModalOptions: {
        mobileLinks: ['trust', 'tokenpocket', 'metamask', 'mathwallet'],
      },
    });
    await walletConnect.enable();
    this.provider = new providers.Web3Provider(walletConnect).getSigner()
    this.connectState = 2
    localStorage.setItem('connectState', 2)
  }

  init() {
    // 初始化只读网络
    this.initOnlyReadNetwOrk()
    if (this.connectState && this.connectState != 2) {
      this.connect(this.connectState, this.connectKey)
    }
  }

  // 退出钱包
  logout() {
    this.connectState = false
    localStorage.setItem('connectState', false)
    localStorage.setItem('connectKey', '')
    this.accountsChanged([])
  }

  // 链路检测
  async wallet_switchEthereumChain() {
    try {
      if (ethereum.chainId === this.network.ChainID) return true
      await ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: this.network.ChainID}]
      })
      return true
    } catch (error) {
      Tips(error)
      return false
    }
  }

  /**
   * 连接钱包
   * @param { Int } type 连接类型 1: MetaMask 2：walletConnect 3：私钥连接
   * @returns {List} 钱包账号列表
   */
  async connect(type, key) {
    switch (type) {
      case 1:
        this._connectMetaMask()
        break;
      case 2:
        this._walletConnect()
        break;
      case 3:
        this._connectPrivateKey(key)
        break;
    }
  }
  

  /**
   * 检测是否有metaMask
   */
  checkEthereum() {
    if (!this.hasEthereum) {
      ElNotification({
        title: 'Tips',
        message: 'Please install the meta mask plug-in first',
        type: 'error',
      })
      return false;
    }
    return true
  }

  /**
   * 初始化合约
   * @param {String} address 合约地址
   */ 
  initContract(address, abi = BLKTokenABI) {
    if (!this.provider) {
      return false
    }
    return new Contract(address, abi, this.provider)
  }

  // 只读合约
  initOnlyReadNetwOrk() {
    this.provider = new providers.JsonRpcProvider(this.network.Rpcs[0])
    this.ContractWETH = new Contract(this.dex.address, WETHABI, this.provider)
  }

  /**
   * 账号变化
   */
  accountsChanged(list) {
    if (list[0] != this.accounts[0]) {
      this.accounts = list
      import(`@/api/abis/${this.network.ABI}`).then(abi => {
        this.ContractWETH = new Contract(this.dex.address, abi.default, this.provider)
        this.trigger('accountChange', list)
      })
    }
  }

  // 添加代币到钱包
  addTokenToMeta(address, symbol, image) {
    window.ethereum.request({
        method: 'wallet_watchAsset',
        params: {
            type: 'ERC20',
            options: {
                address,
                symbol,
                decimals: 18,
                image 
            }
        }
    })
  }

  // 切换到其他网络
  async switchToOtherNetwork(network, dex) {
    const { 
      ChainID: chainId, 
      Name: chainName, 
      Rpcs 
    } = network
    switch (this.connectState) {
      case 1:
      case 2:
        try {
          await this.provider.provider.jsonRpcFetchFunc('wallet_switchEthereumChain', [{ chainId}])
          ElNotification({
            title: 'Success',
            message: 'Network switching succeeded',
            type: 'Success',
          })
          this.network = network
          this.dex = dex
        } catch (switchError) {
          if (switchError.code === 4902) {
            try {
              this.provider.provider.jsonRpcFetchFunc('wallet_addEthereumChain', [
                {
                  chainId, 
                  chainName,
                  rpcUrls: toRaw(Rpcs)
                },
              ])
              ElNotification({
                title: 'Success',
                message: 'Network added successfully',
                type: 'Success',
              })
              this.network = network
              this.dex = dex
            } catch (addError) {
              Tips(addError)
            }
          } else {
            Tips(switchError)
          }
        }
        break;
      case 3:
        {
          const key = localStorage.getItem('connectKey')
          this._connectPrivateKey(key)
          this.network = network
          this.dex = dex
        }
        break;
    }
    
  }
}

export default {
  install: (app, options) => {
    app.config.globalProperties.$wallet = new Wallet(app, options.network)
    window.wallet = app.config.globalProperties.$wallet
  }
}

