import { ElNotification } from 'element-plus'
// 防抖
export const debounce = (fn, wait, immediate) => {
  let timer
  return function() {
    if (timer) clearTimeout(timer)
    if (immediate) {
      // 如果已经执行过，不再执行
      var callNow = !timer
      timer = setTimeout(() => {
        timer = null
      }, wait)
      if (callNow) {
        fn.apply(this, arguments)
      }
    } else {
      timer = setTimeout(() => {
        fn.apply(this, arguments)
      }, wait)
    }
  }
}

// 保留几位小数
export const getBit = (value, bit = 3) => {
  let str = value.toString();
  let strIndex = str.indexOf('.');
  if (strIndex === -1) return str;
  str = str.substring(0, strIndex + bit);
  return str;
}

// 错误提示
export const Tips = (error, title = 'Tips') => {
  ElNotification({
    type: 'error',
    title,
    message: error.data ? error.data.message || error.message : error.message
  })
}

