<style lang="scss">
body {
  padding: 0 !important;
}
.el-icon-success{
  font-size: 30px;
}
#app {
  font-family: Inter, Roboto, Oxygen, Fira Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  font-size: 0.18rem;
  background: rgba(24, 24, 24, 1);
  color: #fff;
}
.app-content{
  width: calc(100% - 20px) !important;
  height: 100%;
  overflow: auto;
  margin: 10px;
  border: 1px solid #2A2A2A;
  border-radius: 18px;
  padding: 10px;
}
.w-main {
  max-width: 16.32rem;
  margin: 0 auto;
  width: 100%;
  padding: 0rem 0.4rem;
}
